
 
 .history-table{
  width: 100%;
  height: 100%;
  background-color: white;
  border-color: #e8e8e8;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;
  border-radius: 5px;
  box-shadow: #dfe5f9 0px 1px 3px 0px, #dfe5f9 0px 0px 0px 1px;
  overflow: hidden;
  
  .heading{
      width: 100%;
      margin-bottom: 20px;
      background-color: #f9f9f9;
  }
  .head-his {
    width: 100%;
    height: 10%;
  //   background-color: #f9f9f9;
  background-color: #f9f9f9;
    color: #121c2d;
    .head-row {
      width: 100%;
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      color: #121c2d;

      

      .id {
        width: 10%;
        //   border: 2px solid red;
        height: 100%;
        padding: 5px 10px;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        border-bottom: 1px solid #e8e8e8;
        border-right: 1px solid #e8e8e8;
        font-size: 0.85vw;
        font-weight: 500;
      }
      .summary {
          width: 40%;
          //   border: 2px solid red;
          height: 100%;
          padding: 5px 10px;
          display: flex;
          justify-content: flex-start;
          align-items: center;
          border-bottom: 1px solid #e8e8e8;
          border-right: 1px solid #e8e8e8;
          font-size: 0.85vw;
          font-weight: 500;
        }
      .r-b {
        width: 17.5%;
        height: 100%;
        // border: 2px solid red;
        height: 100%;
        padding: 5px 10px;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        border-bottom: 1px solid #e8e8e8;
        border-right: 1px solid #e8e8e8;
        font-size: 0.85vw;
        font-weight: 500;
      }
      .s-status{
        width: 15%;
        // border: 2px solid red;
        height: 100%;
        padding: 5px 10px;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        border-bottom: 1px solid #e8e8e8;
        border-right: 1px solid #e8e8e8;
        font-size: 0.85vw;
        font-weight: 500;
      }
      
    }
  }

  .body {
    width: 100%;
    height: 90%;
    // border: 2px solid green;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
    overflow: scroll;
    .body-row {
      width: 100%;
      height: 14%;
      border-color: white;
      display: flex;
      justify-content: center;
      align-items: center;
      cursor: pointer;
      .id {
         width: 10%;
         //   border: 2px solid red;
         height: 100%;
         padding: 5px 10px;
         display: flex;
         justify-content: flex-start;
         align-items: center;
         border-bottom: 1px solid #e8e8e8;
         border-right: 1px solid #e8e8e8;
         font-size: 0.85vw;
         font-weight: 400;
       }
       .summary {
           width: 40%;
           //   border: 2px solid red;
           height: 100%;
           padding: 5px 10px;
           display: flex;
           justify-content: flex-start;
           align-items: center;
           border-bottom: 1px solid #e8e8e8;
           border-right: 1px solid #e8e8e8;
           font-size: 0.85vw;
           font-weight: 400;
         }
       .r-b {
         width: 17.5%;
         height: 100%;
         // border: 2px solid red;
         height: 100%;
         padding: 5px 10px;
         display: flex;
         justify-content: flex-start;
         align-items: center;
         border-bottom: 1px solid #e8e8e8;
         border-right: 1px solid #e8e8e8;
         font-size: 0.85vw;
         font-weight: 400;
       }
       .s-status{
         width: 15%;
         // border: 2px solid red;
         height: 100%;
         padding: 5px 10px;
         display: flex;
         justify-content: flex-start;
         align-items: center;
         border-bottom: 1px solid #e8e8e8;
         border-right: 1px solid #e8e8e8;
         font-size: 0.85vw;
         font-weight: 400;
       }
    }

    .empty{
      width: 100%;
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: flex-start;
     //  border: 2px solid red;
     margin-top: 100px;
     font-size: 0.9vw;
     color: rgb(93, 114, 144);
    }
  }

  .pass{
      background-color: #b5e3806e;
  }
  .fail{
      background-color: #fa707073;
  }

  .error{
      background-color: #F5F5F5;
  }

  .body::-webkit-scrollbar{
     display: none;
  }
 
}
