.form-group{
    // border: 2px solid red;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    gap: 5px;
    label{
        font-size: 0.9vw;
        font-weight: 600;
        color:#525b6c;
        width: 100%;
    }
    input{
        box-shadow: #dae1e9 0px 0px 1px 0px, #dae1e9 0px 0px 2px 0px;
        width: 100%;
        border: 1px solid #dae1e9;
        border-radius: 2px;
        padding: 8px;
        font-size: 0.9vw;
        color: #485567;
    }
    textarea{
        box-shadow: #dae1e9 0px 0px 1px 0px, #dae1e9 0px 0px 2px 0px;
        width: 100%;
        border-radius: 3px;
        padding: 5px;
        color: #485567;
        font-size: 0.9vw;
        border: 1px solid #dae1e9;
      }
      textarea:focus{
          border: 1px solid rgb(93, 114, 144);
          box-shadow: rgb(93, 114, 144) 0px 0px 2px 0px, rgb(93, 114, 144)  0px 0px 1px 0px;
          outline: none;
      }
    input:focus {
        outline: none;
        border: 1px solid #737791; /* Add your preferred border style */
        box-shadow: 0 0 2px #737791; /* Optional: Add a shadow effect */
      }
}

.error-message {
    color: red;
    font-size: 12px;
    margin-top: 4px;
  }