.script-issue {
  width: 100%;
  height: 100%;

  padding-bottom: 5px;
  .heading {
    width: 100%;
    height: 6%;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    // border: 2px solid red;
    p {
      font-size: 1vw;
      color: #3f4d67;
      font-weight: 500;
      width: fit-content;
    }
    .raise-issue-button {
      width: fit-content;
    }
  }

  .issue-box {
    width: 100%;
    height: 94%;

    .box-content {
      width: 100%;
      height: 100%;
      position: relative;
      overflow: hidden;
      box-shadow:
        #dfe5f9 0px 1px 3px 0px,
        #dfe5f9 0px 0px 0px 1px;
    }
  }

  .summary-new {
    width: 100%;

    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 10px;
    height: fit-content;
    .error {
      border: 1px solid red;
    }
    
    .error-message {
      color: red;
      font-size: 0.8vw;
      font-weight: 400;
    }
    p {
      width: 100%;
      font-size: 1vw;
      color: #485567;
      font-weight: 500;
    }
    textarea {
      box-shadow:
        #dae1e9 0px 0px 1px 0px,
        #dae1e9 0px 0px 2px 0px;
      width: 100%;
      border-radius: 3px;
      padding: 5px;
      color: #485567;
      font-size: 0.8rem;
      border: 1px solid #dae1e9;
    }
    textarea:focus {
      border: 1px solid rgb(93, 114, 144);
      box-shadow:
        rgb(93, 114, 144) 0px 0px 2px 0px,
        rgb(93, 114, 144) 0px 0px 1px 0px;
      outline: none;
    }
  }

  .description-new {
    width: 100%;
   
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 5px;

    .error {
      border: 1px solid red;
    }
    
    .error-message {
      color: red;
      font-size: 0.8vw;
      font-weight: 400;
    }
    p {
      width: 100%;
      font-size: 1vw;
      color: #485567;
      font-weight: 500;
    }
    textarea {
      box-shadow:
        #dae1e9 0px 0px 1px 0px,
        #dae1e9 0px 0px 2px 0px;
      width: 100%;
      border-radius: 3px;
      padding: 5px;
      color: #485567;
      font-size: 0.8rem;
      border: 1px solid #dae1e9;
    }
    textarea:focus {
      border: 1px solid rgb(93, 114, 144);
      box-shadow:
        rgb(93, 114, 144) 0px 0px 2px 0px,
        rgb(93, 114, 144) 0px 0px 1px 0px;
      outline: none;
    }
  }
  .result {
    width: 100%;

    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 5px;
    .error {
      border: 1px solid red;
    }
    
    .error-message {
      color: red;
      font-size: 0.8vw;
      font-weight: 400;
    }
    p {
      width: 100%;
      font-size: 1vw;
      color: #485567;
      font-weight: 500;
    }
    textarea {
      box-shadow:
        #dae1e9 0px 0px 1px 0px,
        #dae1e9 0px 0px 2px 0px;
      width: 100%;
      border-radius: 3px;
      padding: 5px;
      color: #485567;
      font-size: 0.8rem;
      border: 1px solid #dae1e9;
    }
    textarea:focus {
      border: 1px solid rgb(93, 114, 144);
      box-shadow:
        rgb(93, 114, 144) 0px 0px 2px 0px,
        rgb(93, 114, 144) 0px 0px 1px 0px;
      outline: none;
    }
  }

  .submit-new {
    width: fit-content;
    text-align: right;
    // button {
    //   padding: 5px 8px;
    //   font-size: 0.9vw;
    //   background-color: #2f63c7;
    //   color: white;
    //   border: none;
    //   border-radius: 3px;
    //   cursor: pointer;
    // }
  }

  .pop-up.show {
    opacity: 1; /* Fade in */
    transform: scale(1); /* Zoom to full size */
  }
}

.error {
  border: 1px solid red;
}

.error-message {
  color: red;
  font-size: 0.9em;
}