$background_color: #5d5fef;
$lighter_shade: #ffffff;
$background_shade: #fafbfc;
.automation-script {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 10px;

  .automation-heading {
    width: 100%;
    height: 5%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    p {
      font-size: 1vw;
      font-weight: 500;
      color: #3f4d67;
    }
    button {
      // width: 70px;
      padding: 4px 8px;
      background-color: #2f63c7;
      border: none;
      border-radius: 3px;
      cursor: pointer;
      p {
        color: white;
        font-size: 0.9vw;
        font-weight: 500;
      }
    }
  }

  .empty-section {
    width: 100%;
    height: calc(95% - 10px);
    display: flex;
    justify-content: center;
    align-items: center;
    h1 {
      font-size: 1vw;
      font-weight: 500;
      color: #737791;
    }
  }

  .script-list {
    width: 100%;
    height: calc(95% - 10px);
    padding: 10px;
    display: block;
    gap: 10px;
    overflow-y: scroll;
    // border: 2px solid red;
  }
}

.form-group {
  // border: 2px solid red;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
  gap: 5px;
  label {
    width: 100%;
    font-size: 0.85vw;
    font-weight: 600;
    color: #525b6c;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 4px;
  }
  input {
    box-shadow:
      #dae1e9 0px 0px 1px 0px,
      #dae1e9 0px 0px 2px 0px;
    width: 100%;
    border: 1px solid #dae1e9;
    border-radius: 2px;
    padding: 8px;
    font-size: 0.85vw;
    color: #485567;
  }
  textarea {
    box-shadow:
      #dae1e9 0px 0px 1px 0px,
      #dae1e9 0px 0px 2px 0px;
    width: 100%;
    border-radius: 3px;
    padding: 5px;
    color: #485567;
    font-size: 0.85vw;
    border: 1px solid #dae1e9;
  }
  textarea:focus {
    border: 1px solid rgb(93, 114, 144);
    box-shadow:
      rgb(93, 114, 144) 0px 0px 2px 0px,
      rgb(93, 114, 144) 0px 0px 1px 0px;
    outline: none;
  }
  input:focus {
    outline: none;
    border: 1px solid #737791; /* Add your preferred border style */
    box-shadow: 0 0 2px #737791; /* Optional: Add a shadow effect */
  }
}

.script-list::-webkit-scrollbar {
  width: 5px;
  border-radius: 50px;
}

.script-list::-webkit-scrollbar-track {
  background: white;
}

/* Handle */
.script-list::-webkit-scrollbar-thumb {
  background: #ebeef2;
  border-radius: 50px;
}

/* Handle on hover */
.script-list::-webkit-scrollbar-thumb:hover {
  background: #ebeef2;
}
