@import "styles/globals/variables";
.table-bordered {
  width: 100%;
  height: 100%;
  border-collapse: collapse;

  table {
    box-shadow:
      #dfe5f9 0px 1px 3px 0px,
      #dfe5f9 0px 0px 0px 1px;

    thead {
      background-color: #f9f9f9;
      
    }
  }

  th,
  td {
    border: 1px solid #e8e8e8;
    padding: 8px;
    text-align: left;
  }
  th {
    font-size: 0.85vw;
    font-weight: 500;
    color: #121c2d;
    padding: 15px;
  }
}
