.issueDetail {
  width: 100%;
  height: 100%;
  position: absolute;
  right: -100%;
  top: 0px;
  background-color: white;
  transition: right 1s ease-in-out;
  box-shadow: #dfe5f9 0px 1px 3px 0px, #dfe5f9 0px 0px 0px 1px;
  padding: 10px;

  .navigate-section {
    width: 100%;
    height: 5%;

    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    padding: 0px 5px;
    button {
      border: none;
      display: flex;
      justify-content: center;
      align-content: center;
      gap: 3px;
      background-color: white;
      color: rgb(93, 114, 144);
      font-size: 0.9vw;
      cursor: pointer;
    }
  }
  .issue-id {
    width: 100%;
    // border: 2px solid red;
    height: 5%;

    padding: 2px 5px;
    p {
      font-size: 0.9vw;
      font-weight: 500;
      color: #313e4e;
    }
  }
  .issue-summary-and-status {
    width: 100%;
    height: 10%;
    font-size: 1.2vw;
    color: #313e4e;
    font-weight: 500;
    padding: 5px;
    margin-bottom: 15px;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 20px;
    p {
      width: 75%;
    }
    .status {
      width: 25%;
      padding: 10px;
     
      display: flex;
      justify-content: flex-start;
      align-items: center;
      gap: 10px;
      .issue-action{
        // border: 2px solid red;
        align-self: auto;
        position: relative;
       
        button{
          border: none;
         
          padding: 4px 5px;
          color: #3f4d67;
          background-color: white;
          
          display: flex;
          justify-content: center;
          align-items: center;
          gap: 5px;
          cursor: pointer;
          p{
            font-size: 0.8vw;
            font-weight: 600;
          }
          
        }
        .action-list{
          position: absolute;
          top: 100%;
          right: 0px;
           background-color: #ebeef2;
          z-index: 1;
          width: 100%;
          p{
            font-size: 0.8vw;
            font-weight: 400;
            width: 100%;
            text-align: center;
            padding: 4px 5px;
            cursor: pointer;
          }
          p:hover{
            background-color: #2f63c7;
            color: white;
          }
        }
        .hide-action{
          position: absolute;
          top: 100%;
          right: 0px;
          border: 2px solid red;
          z-index: -1;
        }
      }
      
    }
  }

  .issue-detail-box {
    width: 100%;
    height: calc(80% - 20px);
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 20px;
   
    .left-side-issue {
      width: 75%;
      height: 100%;
      display: flex;
      justify-content: flex-start;
      align-items: center;
      flex-direction: column;
      gap: 10px;
      padding: 5px;
      overflow-y: auto;
      .description {
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: flex-start;
        flex-direction: column;
        #desc {
          font-size: 0.9vw;
          padding: 5px;
          color: rgb(93, 114, 144);
        }
      }
      .comments {
        margin-top: 30px;
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: flex-start;
        flex-direction: column;
        gap: 15px;
        p {
          width: 100%;
        }
        .add-comment {
          width: 100%;

          display: flex;
          justify-content: center;
          align-items: flex-start;
          flex-direction: column;
          gap: 10px;
          .left-c {
            width: 100%;
            display: flex;
            justify-content: center;
            align-items: flex-start;
            gap: 10px;
          }
          .right-c {
            width: 100%;

            height: 60px;
            textarea {
              width: 100%;
              height: 100%;
              padding: 5px;
              font-size: 0.9vw;
              font-weight: 500;
              color: #313e4e;
              border: none;
              //   border: 1px solid rgb(93, 114, 144);
              box-shadow: rgb(93, 114, 144) 0px 0px 1px 0px,
                rgb(93, 114, 144) 0px 0px 1px 0px;
            }
            textarea:focus {
              border: 0px solid rgb(93, 114, 144);
              box-shadow: rgb(93, 114, 144) 0px 0px 1px 0px,
                rgb(93, 114, 144) 0px 0px 1px 0px;
              outline: none;
            }
          }

          .add-comment-button{
            width: 100%;
         
            text-align: right;
          }
        }

        .list-comment {
          width: 100%;
          display: flex;
          justify-content: center;
          align-items: flex-start;
          flex-direction: column;
          gap: 10px;
          .single-comment {
            width: 100%;
            display: flex;
            justify-content: center;
            align-items: flex-start;
            gap: 10px;

            .left-c {
              width: 30px;
              display: flex;
              justify-content: center;
              align-items: center;
            }
            .right-c {
              width: calc(100% - 30px);
              display: flex;
              justify-content: center;
              align-items: flex-start;
              flex-direction: column;
              gap: 4px;
              height: fit-content;
              #comment {
                font-size: 0.9vw;
                color: #313e4e;

                font-weight: 400;
              }
            }
          }
        }
      }
    }

    .left-side-issue::-webkit-scrollbar{
      width: 3px;
      display: none;
    }
    
    .left-side-issue::-webkit-scrollbar-track {
      background: #ebeef2;
      border-radius: 10px;
    }
     
    /* Handle */
    .left-side-issue::-webkit-scrollbar-thumb {
      background: #dae1e9; 
      border-radius: 10px;
    }


    .right-side-issue {
      width: 25%;
      height: 100%;
      border-left: 1px solid #dae1e9;
      padding: 10px;
      display: flex;
      justify-content: flex-start;
      align-items: center;
      gap: 10px;
      flex-direction: column;
      .people {
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        gap: 10px;

        .name {
            width: 100%;
            color: #3f4d67;
            font-size: 0.9vw;
            font-weight: 500;
           
          }
        .role-section {
          width: 100%;
          display: flex;
          justify-content: center;
          align-items: center;
          gap: 10px;
          flex-direction: column;

          .stp {
            width: 100%;
            display: flex;
            justify-content: flex-start;
            align-items: center;
            // flex-direction: column;
            // border: 2px solid red;

            p {
            }
          }
        }
      }
    }
  }
}

.show {
  right: 0px;
  // border: 1.5px solid #dae1e9;
  border-radius: 5px;
  // box-shadow: #dfe5f9 0px 1px 3px 0px, #dfe5f9 0px 0px 0px 1px;
  // border: 2px solid red;
}

.hide {
  right: -102%;
  // border: 1.5px solid #dae1e9;
  border-radius: 5px;
  // box-shadow: #dfe5f9 0px 1px 3px 0px, #dfe5f9 0px 0px 0px 1px;
}