@import "styles/globals/variables";

.dialog-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw; /* Full width of the viewport */
    height: 100vh; /* Full height of the viewport */
    z-index: 100;
    background-color: rgba(49, 62, 78, 0.298);
    -webkit-backdrop-filter: blur(2px);
    backdrop-filter: blur(2px);
    display: flex;
    justify-content: center;
    align-items: center;
    .dialog-content {
       width:40%;
        background: $color-white;
        // padding: 20px; 
        border-radius: 4px; 
        box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
        // border: 2px solid red;
        display: flex;
        justify-content: space-between;
        align-items: center;
        flex-direction: column;
        gap: 10px;
        border-radius: 4px;
        overflow: hidden;
      }
  
  .dialog-header{
    width: 100%;
    height: 40px;
    // border: 2px solid red;
    display:flex;
    justify-content: space-between;
    align-items: center;
    padding: 5px 10px;
    background-color: #fafbfc;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
    h3{
      font-size: 0.9vw;
      font-weight: 500;
      // border: 2px solid red;
    }
    .close-icon-dialog{
      border:none;
      background-color: transparent;
       font-size: 0.9vw;
    
    }
  }
  .dialog-body{
    // border: 2px solid red;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 100%;
    padding: 0px 10px;
    gap: 10px;
  }
  .dialog-footer{
    display: flex;
    justify-content: flex-end;
    gap:10px;
    // border: 2px solid red;
    width: 100%;
    padding: 10px 10px;
  }
}
