.last-run-table {
  width: 100%;
  height: 95%;
  background-color: white;
  border-color: #e8e8e8;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;
  border-radius: 5px;
  box-shadow:
    #dfe5f9 0px 1px 3px 0px,
    #dfe5f9 0px 0px 0px 1px;
  overflow: hidden;
  .heading {
    width: 100%;
    margin-bottom: 20px;
  }
  .head-run {
    width: 100%;
    height: 10%;
    //   background-color: #f9f9f9;
    color: #121c2d;
    .head-row {
      width: 100%;
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      color: #121c2d;

      .id-n {
        width: 10%;
        //   border: 2px solid red;
        height: 100%;
        padding: 5px 10px;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        border-bottom: 1px solid #e8e8e8;
        border-right: 1px solid #e8e8e8;
        font-size: 0.85vw;
        font-weight: 500;
      }
      .natco {
        width: 15%;
        //   border: 2px solid red;
        height: 100%;
        padding: 5px 10px;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        border-bottom: 1px solid #e8e8e8;
        border-right: 1px solid #e8e8e8;
        font-size: 0.85vw;
        font-weight: 500;
      }
      .modified {
        width: 20%;
        //   border: 2px solid red;
        height: 100%;
        padding: 5px 10px;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        border-bottom: 1px solid #e8e8e8;
        border-right: 1px solid #e8e8e8;
        font-size: 0.85vw;
        font-weight: 500;
      }
      .language {
        width: 15%;
        height: 100%;
        // border: 2px solid red;
        height: 100%;
        padding: 5px 10px;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        border-bottom: 1px solid #e8e8e8;
        border-right: 1px solid #e8e8e8;
        font-size: 0.85vw;
        font-weight: 500;
      }
      .device {
        width: 15%;
        // border: 2px solid red;
        height: 100%;
        padding: 5px 10px;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        border-bottom: 1px solid #e8e8e8;
        border-right: 1px solid #e8e8e8;
        font-size: 0.85vw;
        font-weight: 500;
      }
      .applicable {
        width: 25%;
        // border: 2px solid red;
        height: 100%;
        padding: 5px 10px;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        border-bottom: 1px solid #e8e8e8;
        border-right: 1px solid #e8e8e8;
        font-size: 0.85vw;
        font-weight: 500;
