.edit-box-section{
    // border: 2px solid red;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 5px;

    label{
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 2px;
    }

    .status-actions{
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 5px;
        button{
            font-size: 0.7vw;
            border: none;
            padding: 5px;
            background-color: #e8e8e8;
            border-radius: 2px;
            cursor: pointer;
        }
    }
}
