
.singleactivity{
    width: 100%;
    height: fit-content;
    position: relative;
    border-left: 1.5px solid #dfe5f9;

    .activity-icon{
        position: absolute;
        z-index: 10;
        width: 20px;
        height: 20px;
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 50%;
        left: -10px;
        background-color: #dfe5f9;
        padding: 4px;
    }

    .inner-single-activity{
        width: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      margin-left: 30px;
      padding-bottom: 20px;
      gap: 5px;

      .date-changed-by-name{
        width: 100%;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        padding: 4px 0px;
        
        p{
            font-size: 0.8vw;
            padding: 0px 5px;
            color: rgb(93, 114, 144);
        }
      }

      .reason{
        width: 100%;
        p{
            font-size: 1vw;
            font-weight: 500;
            padding: 0px 5px;
            color: #455264;

        }
      }
      .what-changed{
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;

        .changes{
            width: 100%;
            padding: 0px 5px;
            display: flex;
            justify-content: flex-start;
            align-items: center;
            gap: 6px;
            font-size: 0.9vw;
            font-weight: 400;
            color: rgb(93, 114, 144);
            .normal{
                font-size: 0.9vw;
                font-weight: 400;
                color: rgb(93, 114, 144);
            }

            .diff{
                font-size: 0.9vw;
                font-weight: 500;
                color: #455264;
            }
        }
      }
    }
}