* {
  box-sizing: border-box;
}

html {
  font-size: 16px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  box-sizing: border-box;
  -webkit-text-size-adjust: 100%;
}

body {
  margin: 0;
}

html,
body {
  height: 100vh;
  overflow: overlay;
}

ul {
  padding: 0;
  margin: 0;
  list-style: none;
}
