.activity {
  // border: 2px solid red;
  width: 100%;
  height: 100%;
 
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 20px;

  .heading-activity{
    width: 100%;
    height: 6%;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;

   
    p {
      font-size: 1vw;
      font-weight: 500;
      color: #3f4d67;
      font-weight: 500;
    }
  }

  .activity-list{
    width: 100%;
    height: calc(94% - 20px);
    padding: 5px 20px;
     overflow-y: scroll;
     overflow-x: hidden;
  }

  

}

.activity-list::-webkit-scrollbar {
  width: 3px;
}

/* Track */
.activity-list::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px grey; 
  border-radius: 10px;
}
 
/* Handle */
.activity-list::-webkit-scrollbar-thumb {
  background: #3f4d67; 
  border-radius: 10px;
}